* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #252526;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.NavBar {
    background-color: #333333;
    display: flex;
    justify-content: center;
}

.list {
    padding: 10px;
    list-style-type: none;
}

.link {
    color: white;
    text-decoration: none;
}

.link:hover {
    text-decoration: underline;
}
.text-white {
    color: white;
}

.text-center {
    text-align: center;
}

.pad-25-t{
    padding-top: 25px;
}

.no-link {
    color: black;
    text-decoration: none;
}
.profile-container {
    margin-bottom: 10px;
    display: flex;
    border-radius: 50px;
    height: 100px;
    width: 310px;
    background-color: #f5f5f5f5;
}

.title-holder{
    margin: 0;
    padding-top: 20px;
    padding-left: 10px;
}

.profile-picture{
    margin: 0;
    padding-top: 10px;
    padding-left: 15px;
    border-radius: 100%;
    height: 80%;
}

.profile-title {
    margin: 0;
    padding-top: 5px;
}

.profile-position {
    margin: 0;
    padding-bottom: 5px;
}
.DevCard-container{
    display: flex;
    justify-content: center;
}
